'use client'

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { Trash2, Plus, TrendingUp, TrendingDown } from 'lucide-react'

interface WatchlistItem {
  id: string
  symbol: string
  name: string
  price: number
  change: number
  changePercent: number
}

export function WatchlistTab() {
  const [watchlist, setWatchlist] = useState<WatchlistItem[]>([])
  const [newSymbol, setNewSymbol] = useState('')
  const [isLoading, setIsLoading] = useState(false)

  useEffect(() => {
    // Load watchlist from localStorage
    const savedWatchlist = localStorage.getItem('watchlist')
    if (savedWatchlist) {
      setWatchlist(JSON.parse(savedWatchlist))
    } else {
      // Add some default symbols
      const defaultWatchlist: WatchlistItem[] = [
        { id: '1', symbol: 'RELIANCE', name: 'Reliance Industries', price: 2500, change: 25, changePercent: 1.01 },
        { id: '2', symbol: 'TCS', name: 'Tata Consultancy Services', price: 3200, change: -15, changePercent: -0.47 },
        { id: '3', symbol: 'HDFC', name: 'HDFC Bank', price: 1650, change: 12, changePercent: 0.73 },
        { id: '4', symbol: 'INFY', name: 'Infosys', price: 1450, change: 8, changePercent: 0.56 }
      ]
      setWatchlist(defaultWatchlist)
      localStorage.setItem('watchlist', JSON.stringify(defaultWatchlist))
    }
  }, [])

  const saveWatchlist = (newWatchlist: WatchlistItem[]) => {
    setWatchlist(newWatchlist)
    localStorage.setItem('watchlist', JSON.stringify(newWatchlist))
  }

  const addSymbol = async () => {
    if (!newSymbol.trim()) return

    setIsLoading(true)
    try {
      // In a real app, you'd fetch symbol data from an API
      // For now, we'll simulate adding a symbol
      const newItem: WatchlistItem = {
        id: Date.now().toString(),
        symbol: newSymbol.toUpperCase(),
        name: `${newSymbol.toUpperCase()} Ltd`, // Mock name
        price: Math.floor(Math.random() * 5000) + 100,
        change: Math.floor(Math.random() * 200) - 100,
        changePercent: Math.floor(Math.random() * 10) - 5
      }

      saveWatchlist([...watchlist, newItem])
      setNewSymbol('')
    } catch (error) {
      console.error('Error adding symbol:', error)
    } finally {
      setIsLoading(false)
    }
  }

  const removeSymbol = (id: string) => {
    const updatedWatchlist = watchlist.filter(item => item.id !== id)
    saveWatchlist(updatedWatchlist)
  }

  const refreshPrices = () => {
    // Simulate price updates
    const updatedWatchlist = watchlist.map(item => ({
      ...item,
      price: item.price + (Math.random() - 0.5) * 100,
      change: (Math.random() - 0.5) * 50,
      changePercent: (Math.random() - 0.5) * 2
    }))
    saveWatchlist(updatedWatchlist)
  }

  return (
    <div className="p-6 space-y-6">
      <div className="flex justify-between items-center">
        <h2 className="text-2xl font-bold">Watchlist</h2>
        <Button onClick={refreshPrices} variant="outline">
          Refresh Prices
        </Button>
      </div>

      <Card>
        <CardHeader>
          <CardTitle>Add Symbol</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="flex space-x-2">
            <div className="flex-1">
              <Label htmlFor="symbol">Symbol</Label>
              <Input
                id="symbol"
                placeholder="Enter symbol (e.g., RELIANCE, TCS)"
                value={newSymbol}
                onChange={(e) => setNewSymbol(e.target.value)}
                onKeyPress={(e) => e.key === 'Enter' && addSymbol()}
              />
            </div>
            <div className="flex items-end">
              <Button onClick={addSymbol} disabled={isLoading}>
                <Plus className="w-4 h-4 mr-2" />
                {isLoading ? 'Adding...' : 'Add'}
              </Button>
            </div>
          </div>
        </CardContent>
      </Card>

      <Card>
        <CardHeader>
          <CardTitle>My Watchlist</CardTitle>
        </CardHeader>
        <CardContent>
          {watchlist.length === 0 ? (
            <p className="text-gray-500 text-center py-8">No symbols in watchlist. Add some symbols to get started.</p>
          ) : (
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Symbol</TableHead>
                  <TableHead>Name</TableHead>
                  <TableHead>Price</TableHead>
                  <TableHead>Change</TableHead>
                  <TableHead>Change %</TableHead>
                  <TableHead>Actions</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {watchlist.map((item) => (
                  <TableRow key={item.id}>
                    <TableCell className="font-medium">{item.symbol}</TableCell>
                    <TableCell>{item.name}</TableCell>
                    <TableCell className="font-mono">₹{item.price.toFixed(2)}</TableCell>
                    <TableCell className={`font-mono flex items-center ${item.change >= 0 ? 'text-green-600' : 'text-red-600'}`}>
                      {item.change >= 0 ? (
                        <TrendingUp className="w-4 h-4 mr-1" />
                      ) : (
                        <TrendingDown className="w-4 h-4 mr-1" />
                      )}
                      {item.change >= 0 ? '+' : ''}{item.change.toFixed(2)}
                    </TableCell>
                    <TableCell className={`font-mono ${item.changePercent >= 0 ? 'text-green-600' : 'text-red-600'}`}>
                      {item.changePercent >= 0 ? '+' : ''}{item.changePercent.toFixed(2)}%
                    </TableCell>
                    <TableCell>
                      <Button
                        variant="outline"
                        size="sm"
                        onClick={() => removeSymbol(item.id)}
                      >
                        <Trash2 className="w-4 h-4" />
                      </Button>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          )}
        </CardContent>
      </Card>
    </div>
  )
}