'use client'

import React from 'react'

interface SymbolSelectProps {
  value: string
  onChange: (s: string) => void
  exchange?: string
  segment?: string
}

export default React.memo(function SymbolSelect({ value, onChange, exchange = 'NSE', segment = 'Options' }: SymbolSelectProps) {
  const [open, setOpen] = React.useState(false)
  const [query, setQuery] = React.useState('')
  const [instruments, setInstruments] = React.useState<Array<any>>([])
  const [loading, setLoading] = React.useState(false)
  const containerRef = React.useRef<HTMLDivElement | null>(null)
  const loadedRef = React.useRef(false)

  // fetch instruments from backend proxy
  const BACKEND_BASE = (process.env.NEXT_PUBLIC_API_BASE_URL as string) || 'http://localhost:5000'

  const fetchInstrumentsRef = React.useRef<((searchQuery?: string, force?: boolean) => Promise<void>) | null>(null)

  // Load instruments based on search query and filters.
  // Cache search results in sessionStorage with query as key.
  const fetchInstruments = React.useCallback(async (searchQuery = '', force = false) => {
    const cacheKey = `instruments_search_${exchange}_${segment}_${searchQuery}`
    if (!force && sessionStorage.getItem(cacheKey)) {
      const cached = sessionStorage.getItem(cacheKey)
      if (cached) {
        try {
          const parsed = JSON.parse(cached)
          if (Array.isArray(parsed) && parsed.length > 0) {
            setInstruments(parsed)
            setLoading(false)
            loadedRef.current = true
            return
          }
        } catch (e) {
          // ignore cache parse errors
        }
      }
    }
    
    setLoading(true)
    try {
      // Build URL with exchange and segment filters
      let url = `${BACKEND_BASE}/api/symbols/search?limit=100`
      if (searchQuery) {
        url += `&q=${encodeURIComponent(searchQuery)}`
      }
      if (exchange) {
        url += `&exchange=${encodeURIComponent(exchange)}`
      }
      if (segment) {
        url += `&segment=${encodeURIComponent(segment)}`
      }
      
      console.log('[SymbolSelect] Fetching:', url)
      const resp = await fetch(url)
      const json = await resp.json()
      let list = json && json.data ? json.data : json
      if (!Array.isArray(list) && list && typeof list === 'object') list = Object.values(list)
      if (!Array.isArray(list)) list = []

      console.log('[SymbolSelect] Loaded', list.length, 'symbols for', exchange, segment)
      setInstruments(list)
      try { sessionStorage.setItem(cacheKey, JSON.stringify(list)) } catch (e) { /* ignore */ }
      loadedRef.current = true
    } catch (err) {
      console.error('Error loading instruments', err)
      setInstruments([])
    } finally {
      setLoading(false)
    }
  }, [BACKEND_BASE, exchange, segment])

  // Initial load on mount or when exchange/segment changes
  React.useEffect(() => {
    loadedRef.current = false // Reset when filters change
    fetchInstruments('', true) // Force reload with new filters
  }, [exchange, segment, fetchInstruments])

  // Search when query changes
  React.useEffect(() => {
    if (!query.trim()) return
    
    const timer = setTimeout(() => {
      fetchInstruments(query, false)
    }, 300)

    return () => clearTimeout(timer)
  }, [query, fetchInstruments])

  const symbols = React.useMemo(() => {
    const mapped = instruments.map((it: any) => {
      const symbolName = (it.symbol || it.tradingsymbol || it.tradingSymbol || '').toString()
      const segment = (it.segment || '').toString().toUpperCase()
      const instrumentType = (it.instrument_type || it.instrumentType || '').toString().toUpperCase()
      
      // Check if it's an index: segment='Index' or instrument_type contains 'INDEX' or 'AMXIDX'
      const isIndex = segment === 'INDEX' || 
                      instrumentType === 'INDEX' || 
                      instrumentType === 'AMXIDX' ||
                      instrumentType.includes('IDX')
      
      return {
        key: symbolName,
        display: symbolName,
        group: isIndex ? 'Index' : 'Stock',
        raw: it,
      }
    })
    
    // Debug: log categorization
    const indices = mapped.filter(s => s.group === 'Index').length
    const stocks = mapped.filter(s => s.group === 'Stock').length
    console.log(`[SymbolSelect] Categorized: ${indices} indices, ${stocks} stocks from ${instruments.length} items`)
    
    return mapped
  }, [instruments])

  const grouped = React.useMemo(() => {
    const byGroup: Record<string, any[]> = { Index: [], Stock: [] }
    symbols.forEach(s => {
      (byGroup[s.group] ||= []).push(s)
    })
    return byGroup
  }, [symbols])

  const filtered = React.useMemo(() => {
    const q = query.trim().toLowerCase()
    const filterList = (arr: any[]) => arr.filter(x => x.display.toLowerCase().includes(q))
    return {
      Index: q ? filterList(grouped.Index) : grouped.Index,
      Stock: q ? filterList(grouped.Stock) : grouped.Stock,
    }
  }, [grouped, query])

  React.useEffect(() => {
    function onDocClick(e: MouseEvent) {
      if (!containerRef.current) return
      if (!containerRef.current.contains(e.target as Node)) setOpen(false)
    }
    document.addEventListener('click', onDocClick)
    return () => document.removeEventListener('click', onDocClick)
  }, [])

  return (
    <div className="relative" ref={containerRef}>
      <div
        role="button"
        tabIndex={0}
        onClick={() => setOpen(v => !v)}
        onKeyDown={(e) => { if (e.key === 'Enter') setOpen(v => !v) }}
        className="w-full px-2 py-1.5 text-xs border border-gray-300 rounded bg-white cursor-pointer flex items-center justify-between"
      >
        <span className="truncate">{value || 'Select symbol'}</span>
        <svg className="w-3 h-3 text-gray-500" viewBox="0 0 20 20" fill="currentColor"><path fillRule="evenodd" d="M5.23 7.21a.75.75 0 011.06.02L10 10.94l3.71-3.71a.75.75 0 111.06 1.06l-4.24 4.24a.75.75 0 01-1.06 0L5.21 8.29a.75.75 0 01.02-1.06z" clipRule="evenodd"/></svg>
      </div>

      {open && (
        <div className="absolute z-40 mt-1 w-full bg-white border border-gray-200 rounded shadow-lg">
          <div className="p-2">
            <input
              value={query}
              onChange={(e) => setQuery(e.target.value)}
              placeholder="Search symbol..."
              className="w-full px-2 py-1 text-sm border border-gray-200 rounded focus:outline-none"
            />
          </div>

          <div className="max-h-64 overflow-auto text-sm">
            {loading ? (
              <div className="px-3 py-4 text-center text-xs text-gray-500">Loading symbols...</div>
            ) : instruments.length === 0 ? (
              <div className="px-3 py-3 text-center text-xs text-gray-500">
                No instruments returned from server.
                <div className="mt-2">
                  <button
                    onClick={async () => {
                      setLoading(true)
                      try {
                        const resp = await fetch(`${BACKEND_BASE}/api/symbols/search?q=${encodeURIComponent(query)}&limit=100`)
                        const json = await resp.json()
                        let list = json && json.data ? json.data : json
                        if (!Array.isArray(list) && list && typeof list === 'object') list = Object.values(list)
                        if (!Array.isArray(list)) list = []
                        setInstruments(list)
                      } catch (e) {
                        console.error('Refresh failed', e)
                      } finally {
                        setLoading(false)
                      }
                    }}
                    className="px-3 py-1 text-xs bg-blue-500 text-white rounded"
                  >
                    Refresh
                  </button>
                </div>
              </div>
            ) : (
              ['Index','Stock'].map(group => (
                <div key={group} className="px-2 py-1">
                  <div className="text-xs text-gray-500 font-semibold px-1 py-1">{group}</div>
                      {filtered[group as keyof typeof filtered].length === 0 ? (
                    <div className="text-xs text-gray-400 px-2 py-1">No matches</div>
                  ) : (
                    filtered[group as keyof typeof filtered].map((sym: any) => (
                      <div
                        key={sym.key}
                        onClick={() => { 
                          onChange(sym.display); 
                          setOpen(false); setQuery('') 
                        }}
                        className={`px-2 py-1 cursor-pointer rounded hover:bg-gray-100 ${value === sym.display ? 'bg-gray-100 font-medium' : ''}`}
                      >
                        {sym.display}
                      </div>
                    ))
                  )}
                </div>
              ))
            )}
          </div>
        </div>
      )}
    </div>
  )
})
